const selectedPhotoData = () => {
    if (!window.selectedPhotoData)
        return;
    let id = document.querySelector('.photos-slider__main .swiper-slide-active .photos-slider__item').getAttribute('data-id');
    window.selectedPhotoData.id = id;
}
const bigSlides = [],
    smallSlides = [];

window.photosSliderInit = (targetSlideIndex) => {

    const thumbsSlider = document.querySelector('.photos-slider__nav .swiper-list'),
        mainSlider = document.querySelector('.photos-slider__main .swiper-list');

    let photosSliderThumbs;

    let cloneArrow = document.querySelector('.photos-slider__arrow');
    document.querySelector('.photos-slider__bot').append(cloneArrow);


    $('body').addClass('js__swiper--visible');


    if (mainSlider)
        if (!mainSlider.classList
            .contains("swiper-container-initialized")
        ) {
            window.galleryPhotos.forEach(photo => {
                bigSlides.push(`
					<div data-id="${photo.fileId}" data-index="${photo.index}" class="photos-slider__item">
						<img src="${photo.big.src}" alt="${photo.full.alt}"/>
					</div>
				`);

                smallSlides.push(`
					<div data-id="${photo.fileId}" data-index="${photo.index}" class="photos-slider__item">
						<img src="${photo.small.src}" alt="${photo.full.alt}"/>
					</div>
				`);
            });

            if (thumbsSlider)
                photosSliderThumbs = new Swiper(thumbsSlider, {
                    slidesPerView: 9,
                    spaceBetween: 25,
                    watchSlidesVisibility: true,
                    watchSlidesProgress: true,
                    // simulateTouch: false,
                    // lazy: {
                    // 	loadPrevNext: true,
                    // 	loadPrevNextAmount: 9,
                    // },
                    virtual: {
                        cache: true,
                        slides: smallSlides
                    },
                    breakpoints: {
                        320: {
                            slidesPerView: 2
                        },
                        375: {
                            slidesPerView: 3
                        },
                        660: {
                            slidesPerView: 5
                        },
                        1000: {
                            slidesPerView: 6
                        },
                        1100: {
                            slidesPerView: 9
                        }
                    },
                    on: {
                        slideChangeTransitionEnd() {
                            this.update();
                        }
                    },

                });

            // console.log(targetSlideIndex);

            new Swiper(mainSlider, {
                // slidesPerView: "auto",
                spaceBetween: 60,
                initialSlide: targetSlideIndex,
                // freeMode: true,
                watchSlidesVisibility: true,
                watchSlidesProgress: true,
                mousewheel: true,
                simulateTouch: false,
                virtual: {
                    cache: true,
                    slides: bigSlides,
                    addSlidesBefore: 4
                },
                keyboard: {
                    enabled: true
                },
                thumbs: {
                    swiper: photosSliderThumbs,
                    multipleActiveThumbs: false
                },
                // lazy: {
                // 	loadOnTransitionStart: true,
                // 	loadPrevNext: true,
                // 	loadPrevNextAmount: 2
                // },
                navigation: {
                    nextEl: '.photos-slider .swiper-button-next',
                    prevEl: '.photos-slider .swiper-button-prev',
                },
                on: {
                    init() {
                        // selectedPhotoData();
                    },

                    slideChangeTransitionEnd() {
                        // selectedPhotoData();

                        this.update();

                        // подставляю ссылку для скачивания
                        let activeSlideSrc = document.querySelector('.swiper-slide-visible.swiper-slide-active img').getAttribute('src');
                        document.querySelector('.photos-slider__download').setAttribute('href', activeSlideSrc)
                    }
                },

            });
            // mainSlider.params.control = thumbsSlider;
            // thumbsSlider.params.control = mainSlider;

        } else {
            mainSlider.swiper.slideTo(targetSlideIndex);
        }

}


