document.addEventListener("DOMContentLoaded", () => {


    const playerTextAppend = () => {

        document.querySelectorAll('.tabs__content.active .sg__team-el').forEach((player, key) => {

            console.log(player);

            if (key > 5) return;


            // if(player.hasAttribute('data-id')){
            let playerId = key;

            // if(playerId == document.querySelector(`.sg-map foreignObject[data-id="${playerId}"]`))
            document.querySelector(`.sg-map foreignObject[data-id="${playerId}"] .player__text`).innerHTML = player.innerText;


            // }
        })
    }

    playerTextAppend();

    // переключение табов на странице последний матч
    document.querySelectorAll(".structure-game__teams .tabs__tab").forEach(tab => {

        tab.addEventListener('click', () => {

            let currentBtn = tab,
                currentBtnId = currentBtn.getAttribute("data-id"),
                parent = currentBtn.closest(".tabs");


            if (parent.querySelector('.tabs__tab.active'))
                parent.querySelector('.tabs__tab.active').classList.remove("active");

            if (parent.querySelector(".tabs__content.active"))
                parent.querySelector(".tabs__content.active").classList.remove("active");

            currentBtn.classList.add("active");
            document.querySelector(".tabs__tab[data-id='" + currentBtnId + "']").classList.add("active");
            document.querySelector(".tabs__content[data-id='" + currentBtnId + "']").classList.add("active");


            playerTextAppend();
        })
    });


    // таб в табах
    document.querySelectorAll('.player-characteristics .tabs__tab, .tabs.st__statistics .tabs__tab, .results .tabs__tab').forEach(tab => {

        tab.addEventListener('click', () => {

            let tabId = tab.getAttribute("data-id"),
                parent = tab.closest(".tabs");

            if (tab.classList.contains('active'))
                return


            parent.querySelector(".tabs__tab.active").classList.remove("active");

            tab.classList.add("active");
            parent.querySelector(`.tabs__tab[data-id="${tabId}"]`).classList.add("active");


            for (let child of parent.children) {

                child.classList.remove('active');

                if (child.getAttribute('data-id') === tabId)
                    child.classList.add('active');


            }


        })
    })

    // faq
    $('.faq__item-top').each(function () {
        $(this).click(function () {
            $(this).toggleClass('js__open');
            $(this).siblings('.faq__item-bottom').slideToggle();
        });
    });

    $('.faq__inner-top').each(function () {
        $(this).click(function () {
            $(this).toggleClass('js__open');
            $(this).siblings('.faq__inner-bottom').slideToggle();
        });
    });

})

const articalMove = () => {

    if (document.querySelectorAll('.articals__navigation').length) {
        let articalNavigation = document.querySelector('.articals__navigation').cloneNode(true);
        document.querySelector('.standart-page__text').append(articalNavigation);
    }
}

document.addEventListener("DOMContentLoaded", articalMove);

const iframeWrap = () => {
    if (document.querySelector('.standart-page__text iframe')) {

        document.querySelectorAll('.standart-page__text iframe').forEach(iframe => {

            let iframeWrap = document.createElement('div'),
                iframeClone = iframe.cloneNode(true);

            iframeWrap.classList.add('iframe-wrap');
            iframe.before(iframeWrap);
            iframe.previousSibling.append(iframeClone);
            iframe.remove();


        })

    }

}

