function hideAll() {
    let docsElTop = document.querySelectorAll('.stage__item-top'),
        docsElBot = document.querySelectorAll('.stage__item-bot');

    for (var i = 0; i < docsElTop.length; i++) {
        docsElTop[i].classList.remove('js__open');
    }

    for (var i = 0; i < docsElBot.length; i++) {
        docsElBot[i].style.height = '0';
    }
}

function showText(textEl) {
    textEl.style.height = textEl.scrollHeight + 'px';
}


// ПЕРЕКЛЮЧЕНИЕ РЕЖИМОВ ЦВЕТА В НОВОСТЯХ
{
    function setCookie(name, value, options = {}) {

        options = {
            path: '/media/news/',
            // при необходимости добавьте другие значения по умолчанию
            ...options
        };

        if (options.expires instanceof Date) {
            options.expires = options.expires.toUTCString();
        }

        let updatedCookie = encodeURIComponent(name) + "=" + encodeURIComponent(value);

        for (let optionKey in options) {
            updatedCookie += "; " + optionKey;
            let optionValue = options[optionKey];
            if (optionValue !== true) {
                updatedCookie += "=" + optionValue;
            }
        }

        document.cookie = updatedCookie;
    }

    //переключатель фона в новостях
    if (document.querySelector('.page-color')) {
        const togglerColor = document.querySelector(".page-color")
        togglerColor.addEventListener("click", () => {
            document.body.classList.toggle('white-mode')
            if (document.body.classList.contains('white-mode')) {
                setCookie('NEWS_WHITE', 'Y')
            } else {
                setCookie('NEWS_WHITE', 'N')
            }
        })


    }

    // Разворачивание билета в "Истории покупок билетов"

    const mtiHistory = document.querySelector('.mti-history');

    if (mtiHistory) {
        mtiHistory.querySelectorAll('.my-tickets__item').forEach((ticket) => {
            let lookMoreButton = ticket.querySelector('.ticket-row__more')
            if (lookMoreButton) {
                lookMoreButton.addEventListener('click', function () {
                    ticket.classList.toggle('active')
                    if (!ticket.classList.contains('active')) {
                        this.innerHTML = 'Показать'
                    } else {
                        this.innerHTML = 'Скрыть'
                    }
                })
                lookMoreButton.addEventListener('touchend', function () {
                    ticket.classList.toggle('active')
                    if (!ticket.classList.contains('active')) {
                        this.innerHTML = 'Показать'
                    } else {
                        this.innerHTML = 'Скрыть'
                    }
                })
            }
        })

    }

}