import {Swiper, Navigation, EffectFade, Thumbs, Lazy, Autoplay, Mousewheel, Keyboard, Virtual, Controller, Pagination} from 'swiper/js/swiper.esm';

Swiper.use([Swiper, Navigation, EffectFade, Thumbs, Lazy, Autoplay, Mousewheel, Keyboard, Virtual, Controller, Pagination]);


let eventsSlider = new Swiper(document.querySelector('.main-events__list-items'), {
    spaceBetween: 95,
    slidesPerView: 'auto',
    navigation: {
        nextEl: document.querySelector('.main-events .swiper-button-next'),
        prevEl: document.querySelector('.main-events .swiper-button-prev'),
    },
    pagination: {
        el: document.querySelector('.main-events .swiper-pagination'),
        type: 'progressbar',
    },
    mousewheel: {
        invert: false,
    },
});


export default function initMainEvents() {
    const section = document.querySelector('.main-events')
    if (!section) return
    let dummy = document.createElement('div')
    let sectionCords
    dummy.classList.add('dummy')
    const open = (e) => {
        section.classList.add('opening')
        document.body.classList.add('main-events-opened')
        sectionCords = section.getBoundingClientRect()
        dummy.style.height = sectionCords.height + 'px'
        dummy.style.margin = '90px 0'
        setTimeout(() => {
            section.insertAdjacentElement('beforebegin', dummy)
            if (window.matchMedia('(min-width: 1024px)').matches) {
                section.setAttribute('style', `position: fixed; top: ${sectionCords.y}px; left: ${sectionCords.x}px; margin-top: 0; width: 100%; max-width: var(--base-width);`)
            } else {
                section.setAttribute('style', `position: fixed; top: ${sectionCords.y}px; left: ${sectionCords.x}px; margin-top: 0; width: 100%; max-width: ${sectionCords.width}px;`)
            }

            section.classList.add('opening')
            setTimeout(() => {
                if (window.matchMedia('(min-width: 1024px)').matches) {
                    section.setAttribute('style', `position: fixed; top: 0px; left: 0px; margin: 0; width: 100%; max-width: 100vw; min-height: 100vh;`)
                } else {
                    section.setAttribute('style', `position: fixed; top: 0px; margin: 0; width: 100%; max-width: ${sectionCords.width}px; min-height: 100vh;`)
                }
            }, 50)
            setTimeout(() => {
                section.classList.remove('opening')
                section.classList.add('opened')
                    eventsSlider.update()
                    if (window.matchMedia('(min-width: 1250px)').matches) {
                        eventsSlider.update()
                    } else {
                        eventsSlider.destroy()
                    }
            }, 500)
        }, 500)

    }

    const close = (e) => {
        section.classList.remove('opened')
        if (window.matchMedia('(min-width: 1024px)').matches) {
            section.setAttribute('style', `position: fixed; top: ${sectionCords.y}px; left: ${sectionCords.x}px; margin-top: 0; width: 100%; max-width: var(--base-width);`)
        } else {
            section.setAttribute('style', `position: fixed; top: ${sectionCords.y}px; left: ${sectionCords.x}px; margin-top: 0; width: 100%; max-width: ${sectionCords.width}px;`)
        }
        setTimeout(() => {
            document.body.classList.remove('main-events-opened')
            dummy.remove()
            section.setAttribute('style', '')
        }, 1000)


    }


    // const list = section.querySelector('.main-events__list-items')
    // const mouseWheelEvt = (event) => {
    //     if (list.doScroll)
    //         list.doScroll(event.wheelDelta > 0 ? "left" : "right");
    //     else if ((event.wheelDelta || event.detail) > 0)
    //         list.scrollLeft -= 75;
    //     else
    //         list.scrollLeft += 75;
    //     return false;
    // }
    // в мозиле не работает


    section.querySelector('.main-events__title').addEventListener('click', open)
    section.querySelector('.main-events__list-button').addEventListener('click', close)

    // if (window.matchMedia('(min-width: 1250px)').matches) {
    //     list.addEventListener('mousewheel', mouseWheelEvt)
    // }

}
