import $ from "jquery";
import {Swiper, Navigation, EffectFade, Thumbs, Lazy, Autoplay, Mousewheel} from 'swiper/js/swiper.esm';

Swiper.use([Swiper, Navigation, EffectFade, Thumbs, Lazy, Autoplay, Mousewheel]);


window.tabSliderInit = () => {
    let sliders = document.querySelectorAll(".slider .swiper-list");

    if (!sliders.length)
        return

    for (let slider of sliders) {
        let sliderObject = slider.closest(".sliders-tab");

        new Swiper(slider, {
            slidesPerView: 4,
            spaceBetween: 60,
            // loop: true,
            navigation: {
                prevEl: sliderObject.querySelector(".swiper-button-prev"),
                nextEl: sliderObject.querySelector(".swiper-button-next")
            },
            watchOverflow: true,
            // autoplay: sliderObject.classList.contains("active"),
            breakpoints: {
                1500: {
                    slidesPerView: 4,
                    spaceBetween: 60,
                },
                1100: {
                    slidesPerView: 4,
                    spaceBetween: 40,
                },
                1000: {
                    slidesPerView: 3,
                    spaceBetween: 40,

                },
                660: {
                    slidesPerView: 3,
                    spaceBetween: 20,

                },
                320: {
                    slidesPerView: 2,
                    spaceBetween: 20,

                }
            }
        });
    }

}

document.addEventListener("DOMContentLoaded", () => {

	window.initTabSliders = () => {
		tabSliderInit();

		$(".sliders-tab .title").click(function () {
			let $this = $(this);

			if ($this.hasClass("active"))
				return

			let sliderId = $this.data("id");

			$this.closest('.sliders-tab').find(".title").removeClass("active");
			$this.addClass("active");

			$this.closest('.sliders-tab').find(".slider").removeClass("active");
			$this.closest('.sliders-tab').find(".slider#" + sliderId).addClass("active");

			slider.update()

		});
	}

	window.initTabSliders();
});